header {
    background-color: #333;
    color: #fff;
    text-align: center;
    padding: 1rem;
}

main {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2rem;
    /* padding: 2rem; */
}

input[type="text"] {
    width: 50%;
    padding: 0.5rem;
    font-size: 1.2rem;
    margin-bottom: 1rem;
}

button {
    background-color: #333;
    color: #fff;
    padding: 0.5rem 1rem;
    font-size: 1.2rem;
    cursor: pointer;
}

#weather {
    width: 50%;
    margin-top: 2rem;
    background-color: #ccc;
    padding: 1rem;
    text-align: center;
}

footer {
    background-color: #333;
    color: #fff;
    text-align: center;
    padding: 1rem;
    margin-top: 2rem;
}
